<template>
    <div class="page-products">
        <h1>Products</h1>
        <div class="products_list">
            <div v-for="product in products" :key="product.id" class="product">
                <img class="product_img" :src="product.picture" :alt="product.name" @click="toggleModal(product)">
                <div class="product_name">{{product.name}}</div>
                <div class="product_bottom">
                    <div class="product_price">${{product.price}}</div>
                    <button class="product_details" @click="toggleModal(product)">Details</button>
                </div>
            </div>
        </div>
        <transition name="fade">
            <div v-if="showModal">
                <Modal @close="toggleModal">
                    <div class="modal_image">
                        <img :src="modal.img" :alt="modal.name">
                    </div>
                    <div class="modal_details">
                        <div class="modal_title">{{modal.name}}</div>
                        <div class="modal_price">${{modal.price}}</div>
                        <div class="modal_subtitle">Description</div>
                        <div class="modal_description">{{modal.description}}</div>
                        <button @click="$router.push({name: 'Thanks'})">
                            Add to Cart
                            <span class="icon-cart"></span>
                        </button>
                    </div>
                </Modal>
            </div>
        </transition>
        <transition name="fade">
            <div v-show="empty" class="empty">No more items</div>
        </transition>
    </div>
</template>

<script>
import Modal from '@/components/Modal'

export default {
    components: { Modal },
    data() {
        return {
            products: [],
            modal: {
                img: '',
                name: '',
                price: '',
                description: '',
            },
            pagination: 1,
            firescroll: false,
            status: '',
            empty: false,
            lastScroll: 0,
            showModal: false,
            url: 'https://test-api.updivision.work/api/products'
        }
    },
    methods: {
        getProducts(page) {
            fetch(this.url + ( page ? `/paginate?page=${page}` : ''), {
                headers: {
                    'Accept': 'application/json',
                    'Authorization': `Bearer ${this.$store.getters.getToken}`, 
                },
            }).then(response => { 
                this.status = response.status;

                response.json()
                .then( (data) => { 
                    if(this.status === 200) {
                        if(page) {
                            this.products = [...this.products, ...data.data];
                            this.pagination = this.pagination + 1;  
                             
                            if(data.data.length) {
                                this.firescroll = true;
                                window.scrollBy(0, -5); // User scrolling area
                            } else {
                                this.firescroll = false;
                                this.empty = true;
                            }

                        } else {
                            this.products = data;
                            this.firescroll = true;
                        }
                    }

                    if(this.status === 401) {
                        this.$router.push({name: 'Login'});
                    }
                })
            }).catch(error => {
                this.errorMsg = error.message;
                this.showError = true;
            });
        },
        toggleModal(item) {
            this.showModal = !this.showModal;

            if(this.showModal) {
                this.modal.img = item.picture;
                this.modal.name = item.name;
                this.modal.price = item.price;
                this.modal.description = item.description;
            }
        },
        infiniteScroll() {
            document.addEventListener('scroll', () => {
                const scrolled = window.scrollY,
                      bodyHeight = document.body.clientHeight,
                      windowHeight = window.innerHeight;

                if (scrolled > this.lastScroll && bodyHeight < windowHeight + scrolled + 10) {
                    if (this.firescroll) {
                        this.firescroll = false;
                        this.getProducts(this.pagination);
                    }
                } 

                this.lastScroll = window.scrollY;
            });
        },
    },
    mounted() {
        this.getProducts();
        this.infiniteScroll();
    }
}
</script>

<style scoped>
.page-products {
    padding-top: 100px;
    text-align: center;
    margin-bottom: 50px;
}
.products_list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 900px;
    margin: 30px auto 0;
}
.product {
    width: calc(25% - 24px);
    background-color: #fff;
    border-radius: 5px;
    padding: 0 10px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 45px 12px 5px;
    box-sizing: border-box;
    box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
}
.product_img {
    cursor: pointer;
    position: relative;
    height: 270px;
    object-fit: cover;
    transform: translateY(-20px);
    box-shadow: 0 10px 15px -8px rgba(0,0,0,0.4);
    border-radius: 8px;
    transition: box-shadow .3s ease-in-out;
}
.product:hover .product_img {
    box-shadow: 0 15px 15px -5px rgba(0,0,0,0.4);
}
.product_name {
    font-size: 15px;
    font-weight: 700;
    margin: 5px 5px 20px;
}
.product_bottom {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
}
.product_price {
    font-size: 14px;
    display: flex;
    align-items: center;
    color: #aaa;
}
.product_details {
    font-size: 9px;
    text-transform: uppercase;
    padding: 5px 15px;
}
.empty {
    font-weight: 700;
}

@media screen and (min-width:769px) {
    .product {
        width: calc(25% - 24px);
    }
}

@media screen and (max-width:768px) {
    .product {
        width: calc(50% - 24px);
    }
}

@media screen and (max-width:380px) {
    .product {
        margin-top: 25px;
        width: calc(100% - 24px);
    }
}
</style>